import React from 'react';

import './cover.css';

import profile from '../../assets/img/profile.jpeg';

const Cover = () => {

    const handleScroll = () => {
        let coverHeight = document.querySelector('.cover');
        let height = coverHeight.offsetHeight;
        window.scrollBy(0, height)
    }


    return (
        <section className="cover">
            <div className="container">
                <img src={profile} className="cover-profile-pic" alt="Hi its me" />
                <div>
                    <h1 className="cover-titulo">Octavio Ibáñez</h1>
                    <h2 className="cover-profession">Ingeniero en informática</h2>
                </div>
            </div>
            <div className="down-arrow">
                <i className="fas fa-angle-down" onClick={handleScroll}></i>
            </div>
        </section>
    );
};

export default Cover;