import React, {useState} from 'react';
import './footer.css';

const Footer = () => {
    const [showMessage, setShowMessage] = useState(false);
    const [data, setData] = useState({
        nombre: '',
        contacto: '',
        mensaje: ''
    });

    const handleForm = e => {
        setData({
            ...data,
            [e.target.name] : e.target.value});
    }

    const handleSubmit = e => {
        e.preventDefault();
        let valida = Object.values(data).every( campo => campo !== "");

        if( !valida ){ return }

        setShowMessage(true);
        setData({
            nombre: '',
            contacto: '',
            mensaje: ''
        });
        setTimeout(function(){
            setShowMessage(false);
        }, 3000);
    }

    return (
        <div className="footer">
            <div className="alert" style={ showMessage? {display: "block"}: {display: "none"}}>
                <div className="message">
                    <p><span><i className="fas fa-paper-plane"></i></span> Mensaje enviado</p>
                    <h2>¡Muchas gracias! <br />Pronto me comunicaré contigo.</h2>
                </div>
            </div>

            <div className="container flex">
                <div className="footer-container">
                   <h2 className="subtitulo light"><i className="fas fa-mug-hot"></i> Charlemos</h2>
               </div>
               <div className="footer-container">
                    <form onSubmit={handleSubmit}>
                        <div className="div-form">
                            <label htmlFor="nombre">Nombre</label>
                            <input type="text" name="nombre" onChange={handleForm} value={data.nombre} />
                        </div>
                        <div className="div-form">
                        <label htmlFor="">Contacto</label>
                            <input type="text" name="contacto" onChange={handleForm} value={data.contacto} />
                        </div>
                        <div className="div-form">
                            <label htmlFor="mensaje">Mensaje</label>
                            <textarea name="mensaje" onChange={handleForm} value={data.mensaje}></textarea>
                        </div>
                        <div className="div-form flex button-container">
                            <button type="submit" className="button">Enviar</button>
                        </div>
                    </form>
               </div>
            </div>
            
        </div>
    );
};

export default Footer;